//活动状态管理
import * as types from '../types'
const state = {
    activityStatus: null,
    join:0,
    picture:{
        picture:null,
        code:"-1",
    },
    updateStatus:0,
    getMoney:false,

};

const getters = {}
const actions = {}

const mutations = {
    [types.UPDATE_ACTIVITY_STATUS](state,payload) {
        state.activityStatus = payload.activityStatus
        console.log(state.activityStatus);
    },
    [types.UPDATE_JOIN](state,payload) {
        state.join = payload.join
    },
    [types.OFFLINE_IMG](state,payload) {
        state.picture = payload
    },
    [types.UPDATE_STATUS](state,payload) {
        state.updateStatus++;
    },
    [types.GET_MONEY](state,payload) {
        state.getMoney = payload;
    },
};

export default {
    state,
    getters,
    mutations,
    actions
}


