/*** IMPORTS FROM imports-loader ***/
var baseEnv = 0;
var isProduction = 0;

import * as LOGLEVEL from './loglevel.js'

export const LOG_LEVEL = LOGLEVEL.DEBUG;
export const PRODUCTION_ENV = baseEnv;    //0是在原生上运行 1 是在手机浏览器上运行  2 是在电脑端浏览器上使用
//获取浏览器类
const ua = navigator.userAgent;
export const isAndroid =PRODUCTION_ENV== 0 && (ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1); //android终端
export const isIOS = PRODUCTION_ENV== 0 && (!!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)); //ios终端

//高德地图key
export const GAODE_MAP_KEY = "16d95a2f157db464a9aa3f3d3a7aa61e";

//接口地址
export const API_BASE_URL = isProduction == 1? "https://m.zhaocaiapp.cn/api" : "http://m.zhaocaiapp.local/api";

//唤醒APP
export const ANDROID_AWAKEN_SCHEMA = "com.zc.zcapp://";
export const IOS_AWAKEN_SCHEMA = "com.zc.zcapp://";

//下载APP
export const ANDROID_DOWNLOAD_URL = "http://www.baidu.com";
export const IOS_DOWNLOAD_URL = "https://itunes.apple.com/cn/app/草根投资-国资系投资理财神器/id912261455?mt=8";



